
body {
  margin: 0;
  width: 100vw;
  height: 100vh;
}
#app {
  position: relative;
  width: 100%; height: 100%;
  background-color: #EDECED;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.overlay {
  position: fixed;
  top: 0; right: 0; bottom: 0; left: 0;
  z-index: 9999;
  background-color: rgba(0, 0, 0, 0.8);
}
@media (orientation: landscape) {
.overlay {
    display: none;
}
}
.overlay img {
  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
  width: 25%;
}
#header {
  position: fixed;
  top: 0; right: 0; left: 0;
  height: 50px;
  background-color: #3748AC;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.header-title {
  margin: 0;
  color: white;
}
#footer {
  position: fixed;
  left: 0; bottom: 0; right: 0;
  height: 50px;
  padding: 4px;
  background-color: black;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
}
.btn-icon {
  border: 0; outline: 0;
  background-color: transparent;
  cursor: pointer;
  margin: 0 16px;
  padding: 0;
}
.modal-watermarking {
  box-sizing: border-box;
  position: absolute;
  top: 50%; left: 50%;
  transform: translate(-50%, -50%);
  width: 80%;
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-flow: column wrap;
      flex-flow: column wrap;
  padding: 32px;
  box-shadow: 0 2px 2px 0 rgba(0,0,0,.14),0 3px 1px -2px rgba(0,0,0,.2),0 1px 5px 0 rgba(0,0,0,.12);
  background-color: #F9F9F9;
}
.modal-watermarking__txt {
  margin: 0;
}
.modal-watermarking__input {
  width: 80%;
  margin: 12px auto;
  font-size: 20px;
}
.modal-watermarking__btn {
  padding: 8px 16px;
  border: 0;
  background-color: #1E8BF1;
  color: white;
}




